.main-block {
  width: 500px;
  background-color: #d3d3d3;
  border: 1px solid tomato;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

.timer {
  width: 100%;
  justify-content: center;
  column-gap: 25px;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}

.start-btn {
  color: #000;
  width: 125px;
  height: 45px;
  background-color: tomato;
  border: 1px solid tomato;
  border-radius: 10px;
  margin: 30px auto auto;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: larger;
  font-weight: 700;
}

.start-btn:hover {
  color: #fff;
  background-color: #000;
}

input {
  color: #000;
  width: 225px;
  height: 25px;
  background-color: tomato;
  border: 2px solid #fff;
  margin: 30px auto auto;
  font-weight: bold;
}

.value {
  color: tomato;
  font-size: larger;
  font-weight: 800;
}

